import { Component } from '@angular/core';
import { faBrain, faDiceD20, faFlask, faWarehouse, faRocket } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'EVEApp';
  currentDate = new Date();
  faBrain = faBrain;
  faDiceD20 = faDiceD20;
  faFlask = faFlask;
  faWarehouse = faWarehouse;
  faRocket = faRocket;
  show(pagetitle) {
    document.getElementById('pagetitle').innerHTML = pagetitle;
    document.getElementById('pagetitle').style.opacity = '1';
  }
  hide() {
    document.getElementById('pagetitle').style.opacity = '0';
    setTimeout(function(){document.getElementById('pagetitle').innerHTML = "";}, 1);
  }
}
